
import { defineComponent, ref } from 'vue'

type TValidator = (value: string) => false | string

export default defineComponent({
  name: 'BaseInput',
  props: {
    label: {
      type: String,
      required: true,
    },
    type: {
      type: String,
      required: false,
      default: 'text',
    },
    validationFunctions: {
      type: Array as () => TValidator[],
      required: false,
      default: () => [() => false] as TValidator[],
    },
    value: {
      type: String,
      required: false,
      default: '',
    },
    hint: {
      type: String,
      required: false,
    },
  },
  emits: ['update:value', 'setValid'],
  setup (_, { emit }) {
    const isValid = ref(true)
    const errorText = ref('')
    const validate = (text: string, uid: string) => {
      let error: boolean | string = false
      for (let fn of _.validationFunctions) {
        error = fn(text)
        if (error) {
          emit('setValid', { state: false, id: `input-${uid}` })
          errorText.value = error
          return
        }
        emit('setValid', { state: true, id: `input-${uid}` })
        errorText.value = ''
      }
    }

    return {
      date: Date.now(),
      isValid,
      errorText,
      validate,
    }
  },
})
