
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'BaseInput',
  props: {
    label: {
      type: String,
      required: true,
    },
    accept: {
      type: String,
      required: false,
      default: '*',
    },
    multiple: {
      type: Boolean,
      required: false,
      default: false,
    },
    value: {
      type: String,
      required: false,
      default: '',
    },
    hint: {
      type: String,
      required: false,
    },
  },
  emits: ['update:value'],
})
